<template>
  <div class="register container">
    <Header headerName="注册"></Header>
    <section>
      <div>
        <input
          type="text"
          placeholder="请输入手机号"
          pattern="[0-9]*"
          v-model="userTel"
        />
      </div>
      <div class="yzm">
        <input type="text" placeholder="请输入图形验证码" /><button>
          Pe67
        </button>
      </div>
      <div class="messageyam">
        <input
          type="text"
          placeholder="请输入短信验证码"
          pattern="[0-9]*"
          v-model="userCode"
        /><button
          @click="sendCode"
          :disabled="disabled"
          :class="disabled ? 'bisabled' : ''"
        >
          获取短信验证码
        </button>
      </div>
      <div>
        <input type="text" placeholder="请设置密码" v-model="userPassword" />
      </div>
      <div class="userlogin" @click="userLoginIn"><b>注册</b></div>
      <span class="khfw">注册视为同意《客户服务协议》</span>
    </section>
    <Tabber></Tabber>
  </div>
</template>

<script>
import Tabber from "@/components/common/Tabber.vue";
import Header from "@/views/login/Header.vue";
import { Toast } from "mint-ui";
import { getMsgCard, userRegister } from "@/api/login.js";
export default {
  name: "register-vue",
  components: {
    Tabber,
    Header,
  },
  data() {
    return {
      rules: {
        //手机号验证
        userTel: {
          rule: /0?(13|14|15|18|17|19)[0-9]{9}/,
          msg: "手机号输入有误",
        },
        //密码验证
        password: {
          rule: /^\w{6,12}$/,
          msg: "请输入6-12位密码",
        },
      },
      userTel: "", //用户手机号
      userCode: "", //用户输入的验证码
      disabled: false, //获取短信验证码按钮是否为灰色
      code: "", //后端返回的验证码
      userPassword: "", //用户密码
    };
  },
  methods: {
    //点击获取短信验证码
    async sendCode() {
      let bool = this.validate();
      if (bool) {
        //发送短信验证码
        clearTimeout(settime);
        this.disabled = true;
        // 请求短信验证码接口
        let res = await getMsgCard(this.userTel);
        if (res.data.code == 200) {
          this.code = res.data.data.data;
        }
      }
      var settime = setTimeout(() => {
        this.disabled = false;
      }, 15000);
    },
    //用户手机号验证
    validate() {
      let bool = true;
      if (!this.rules.userTel.rule.test(this.userTel)) {
        Toast({
          message: this.rules.userTel.msg,
        });
        bool = false;
        return false;
      }
      return bool;
    },
    //用户密码验证
    pwdValidate() {
      let bool = true;
      if (!this.rules.userTel.rule.test(this.userTel)) {
        Toast({
          message: this.rules.userTel.msg,
        });
        bool = false;
        return false;
      }
      if (!this.rules.password.rule.test(this.userPassword)) {
        Toast({
          message: this.rules.userTel.msg,
        });
        bool = false;
        return false;
      }
      return bool;
    },
    async userLoginIn() {
      let flag = this.pwdValidate();
      if (flag) {
        //用户注册成功
        let res = await userRegister(this.userTel, this.userPassword);
        if (res.data.code != 200) {
          Toast({
            message: "用户注册失败",
            duration: 1000,
          });
        } else {
          Toast({
            message: "用户注册成功",
            duration: 1000,
          });
          setTimeout(() => {
            this.$router.push("/passwordLogin");
          }, 1500);
        }
      }
    },
  },
};
</script>

<style scoped lang="less">
section {
  flex: 1;
  overflow: hidden;
  background-color: #f5f5f5;
  padding: 0.5rem 0;
  font-size: 0.32rem;
  div {
    width: 80%;
    height: 1.5rem;
    line-height: 1.5rem;
    margin: 0 auto;
    input {
      height: 1rem;
      width: 80%;
      line-height: 1rem;
      padding: 0 0.3rem;
      background-color: #fff;
      border: 1px solid #ccc;
    }
  }
  .yzm {
    input {
      width: 60%;
    }
    button {
      margin-left: 0.2rem;
      height: 1rem;
      line-height: 1rem;
      background-color: #ccc;
      border: 0;
      font-size: 0.5rem;
      font-weight: 600;
      color: #fff;
    }
  }
  .messageyam {
    input {
      width: 50%;
    }
    button {
      margin-left: 0.2rem;
      width: 35%;
      height: 0.8rem;
      line-height: 0.8rem;
    }
  }
  .userlogin {
    margin-top: 0.5rem;
    height: 1rem;
    background-color: #b0352f;
    line-height: 1rem;
    text-align: center;
    color: #fff;
  }
  .khfw {
    padding: 0 1rem;
    font-size: 0.4rem;
  }
}
</style>
