import request from "./request";
//用户登录接口
export const userPasswordLogin = (tel, pwd) => {
  return request({
    url: "/api/login/passwordLogin",
    method: "post",
    data: {
      tel,
      pwd,
    },
  });
};
//获取短信验证码接口
export const getMsgCard = (phone) => {
  return request({
    url: "/api/login/code",
    method: "post",
    data: {
      phone,
    },
  });
};
//注册接口
export const userRegister = (phone, password) => {
  return request({
    url: "/api/login/adduser",
    method: "post",
    data: {
      phone,
      password,
    },
  });
};
